import React from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { chooseCheese } from "./rootSlice";

export const Step3 = () => {
  const dispatch = useDispatch();
  const history = useHistory()
  const cheese = useSelector(state => state.cheese)
  const { register, handleSubmit } = useForm({ defaultValues: { cheese } });

  const onSubmit = (data) => {
    dispatch(chooseCheese(data.cheese));
    history.push("./step4");
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <div class="form-section-head">
        <div>Address</div>
      </div>
      <div>
        <input ref={register} type="checkbox" name="home" />
        <label htmlFor="cheese">Home:</label>
        <input ref={register} type="checkbox" name="office" />
        <label htmlFor="other">Office:</label>
        <input ref={register} type="checkbox" name="other" />
        <label htmlFor="cheese">Other:</label>
        <hr></hr>
        <label htmlFor="line1">Line 1:</label>
        <input ref={register} type="text" name="line1" />
        <label htmlFor="line2">Line 2:</label>
        <input ref={register} type="text" name="line2" />
 
      </div>
      <button class="btn dark-blue">Next</button>
    </form>
  );
};