import React from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { chooseSauce } from "./rootSlice";
import { Link } from 'react-router-dom'

export const Step4 = () => {
  const dispatch = useDispatch();
  const history = useHistory()
  const sauce = useSelector(state => state.sauce)
  const { register, handleSubmit } = useForm({ defaultValues: { sauce } });

  const onSubmit = (data) => {
    dispatch(chooseSauce(data.sauce));
    history.push("./result");
  };

  return (
    <form >
      <div class="form-section-head">
        <div>Payment</div>
      </div>
      <div class="cta-container">
        <Link class="btn dark-blue" to="/other-features">Cash</Link>
        <Link class="btn blue" to="/other-features">Online</Link>
      </div>
    </form>
  );
};